.app-container{position:relative;-webkit-overflow-scrolling:touch}.app-container nav{display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-align-items:center;-ms-flex-align:center;align-items:center;position:absolute;right:10px;top:10px}.app-container nav .lang,.app-container nav .github{margin-left:10px}.app-container nav .github{color:#333}.app-container nav .setting{width:20px;height:20px;border:4px solid #fff;background:var(--theme-primary);-webkit-box-shadow:0 1px 2px 0 rgba(56,56,56,.15);box-shadow:0 1px 2px 0 rgba(56,56,56,.15);cursor:pointer}.app-container h1{margin:0;padding:15px;font-size:16px;font-weight:400}.setting-container{margin-top:8px;padding:8px;background:#fff;border-radius:4px;-webkit-box-shadow:0 2px 8px 0 rgba(0,0,0,.1);box-shadow:0 2px 8px 0 rgba(0,0,0,.1)}.themes{display:block;margin-top:8px}.colors{display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;list-style:none;margin:0;padding:0}.colors li{display:inline-block;width:25px;height:25px;border-radius:2px;cursor:pointer}.colors li:not(:last-child){margin-right:5px}.zs-popper{z-index:1500}body[data-theme=dark] .github{color:#f1f1f1}body[data-theme=dark] .setting{border-color:#393939}body[data-theme=dark] .setting-container{background:#222;-webkit-box-shadow:0 2px 8px 0 rgba(0,0,0,.6);box-shadow:0 2px 8px 0 rgba(0,0,0,.6)}